import { useState, useEffect, useRef } from "react";
import { Link, Outlet, useNavigate, useLocation } from "react-router-dom";
import { Row, Col, Menu } from "antd";
import { childrenToReact } from "react-markdown/lib/ast-to-react";

const menuData = [
  {
    key: "/",
    label: "首页",
  },
  {
    key: "/travel",
    label: "旅行",
  },
  {
    key: "/user",
    label: "关于",
  },
];

export default () => {
  const location = useLocation();
  const navigate = useNavigate();
  const [selectedKey, setSelectedKey] = useState(location.pathname);

  const contentRef = useRef(null);
  const [contentHeight, setContentHeight] = useState(0);

  console.log("location:", location.pathname);

  const handleMenuSelect = (e) => {
    setSelectedKey(e.key);
    navigate(e.key);
  };

  useEffect(() => {
    if (contentRef.current) {
      const menuHeight = document.querySelector("#fixed-menu").clientHeight;
      setContentHeight(window.innerHeight - menuHeight);
    }
  }, []);

  return (
    <>
      <div
        ref={contentRef}
        style={{ width: "100vw", height: contentHeight, overflow: "auto" }}
      >
        <Outlet />
      </div>

      <div
        id="fixed-menu"
        style={{ position: "fixed", width: "100vw", bottom: 0 }}
      >
        <Menu
          mode="horizontal"
          theme="dark"
          selectedKeys={[selectedKey]}
          onSelect={handleMenuSelect}
          items={menuData}
          // inlineIndent={0}
          style={{
            display: "flex",
            justifyContent: "center",
            // alignItems: "center",
          }}
        >
        </Menu>
      </div>
    </>
  );
};
